/*MOBILE VIEW*/
@media only screen and (max-width: 767px) {
  .calendar {
    display: none;
  }

  .calendar-mobile {
    display: initial;
  }

  .day {
    font-size: xx-small;
  }

  .duration {
    font-size: x-small;
  }
}

/*SUPER SMALL VIEW*/
@media only screen and (max-width: 330px) {
  .calendar {
    display: none;
  }

  .calendar-mobile {
    display: initial;
  }

  .day {
    font-size: xx-small;
  }

  .duration {
    font-size: x-small;
  }
}
